import request from 'superagent'

function requestVideos() {
	return {
		type: 'VIDEOS_REQUEST',
		isFetching: true
	}
}

function requestVideosSuccess(videos) {
	return {
		type: 'VIDEOS_SUCCESS',
		isFetching: false,
		videos
	}
}

export function getPrevVideo() {
	return {
		type: 'VIDEO_PREV_GET'
	}
}

export function getVideo(index) {
	return {
		type: 'VIDEO_GET',
		index
	}
}

export function getVideos() {
	return dispatch => {
		dispatch(requestVideos())
		return request
			.get('/videos')
			.then(res => {
				if (res.body.videos) {
					dispatch(requestVideosSuccess(res.body.videos))
					return res.body.videos.length
				}
			})
	}
}
