import React, {Component} from 'react'
import { browserHistory } from 'react-router'
import { connect } from 'react-redux'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TextField from 'material-ui/TextField';
import Paper from 'material-ui/Paper';

import {Card, CardActions, CardHeader, CardText} from 'material-ui/Card';
import RaisedButton from 'material-ui/RaisedButton';

import Logo from '../Components/Logo'

const paperStyle = {
  height: 50,
  width: 50,
  lineHeight: '50px',
  marginRight: 5,
  textAlign: 'center',
  display: 'inline-block',
};
class App extends Component {
	constructor(props) {
		super(props);
		this.weights = [
			45, 35, 25, 10, 5, 2.5
		]
		this.state = {
			barWeight: 45,
			weight: 0,
			toUse: []
		}
	}
	calculate() {
		console.log('test');
		let weightCounter = 0;
		let weight = (this.state.weight - this.state.barWeight) / 2;
		let used = [];
		while (weight > 0) {
			if (weight >= this.weights[weightCounter]) {
				used.push(this.weights[weightCounter]);
				weight -= this.weights[weightCounter];
			} else {
				weightCounter++;
			}
		}

		this.setState({
			toUse: used
		})
	}

	render() {
		return <MuiThemeProvider>
			<div className="container">
				  <Card>
    <CardHeader
      title="Calculate Weight"
    />
	<CardText>
		<TextField
			floatingLabelText="Bar Weight"
			value={this.state.barWeight}
			type="number"
			onChange={e => {
				this.setState({
					barWeight: parseInt(e.target.value)
				});
		}}
		/>
		<TextField
			floatingLabelText="Weight"
			value={this.state.weight}
			type="number"
			onChange={e => {
				this.setState({
					weight: parseInt(e.target.value)
				});
			}}
		/>
	{this.state.toUse.length > 0 ? <div>You'll need a: {this.state.toUse.map(weight => <Paper style={paperStyle} zDepth={1} circle={true}>{weight}</Paper>
)}</div> : ''}
	</CardText>
    <CardActions>
		<RaisedButton onTouchTap={() => this.calculate()}label="Calculate" fullWidth={true} />
    </CardActions>
  </Card>
			</div>
		</MuiThemeProvider>;
	}
}

export default connect(
	state => ({
	})
)(App);
