const initialState = {
	videos: [],
	prev: [],
	video: {},
	index: 0
}

export default function update(state = initialState, action) {
	switch (action.type) {
		case 'VIDEOS_REQUEST':
			return Object.assign({}, state, {
				isFetching: action.isFetching
			});
		case 'VIDEOS_SUCCESS':
			return Object.assign({}, state, {
				isFetching: action.isFetching,
				videos: action.videos
			});
		case 'VIDEO_GET':
			return Object.assign({}, state, {
				video: state.videos[action.index],
				index: action.index,
				prev: Object.keys(state.video).length > 0 ? [...state.prev, state.video] : []
			})
		case 'VIDEO_PREV_GET':
			return Object.assign({}, state, {
				video: state.prev[state.prev.length - 1],
				prev: state.prev.slice(0, state.prev.length - 1)
			})
		default:
			return state;
	}
}
