import React, {Component} from 'react'
import { connect } from 'react-redux'
import { getVideos, getVideo, getPrevVideo } from '../Actions/Video'
import Logo from '../Components/Logo'

class Player extends Component {
	componentDidMount() {
		this.props.dispatch(getVideos())
			.then((length) => {
				this.props.dispatch(getVideo(Math.floor(Math.random() * (length - 1))))
			})
	}


	getPreviousVideo() {
		if (this.props.videos.prev.length > 0) {
			this.props.dispatch(getPrevVideo());
		}
	}

	getRandomVideo() {
		this.props.dispatch(getVideo(Math.floor(Math.random() * (this.props.videos.videos.length - 1))))
	}

	render() {
		let showPrev = 'button prev ' + (this.props.videos.prev.length > 0 ? '' : 'hidden');
		return(
			<div className="player">
					<Logo size="small" />
					<div className="control"><div className={showPrev} onClick={() => {this.getPreviousVideo()}}></div></div>
					<div className="video">
						<div className="video-wrapper">
							<iframe width="650" height="365" src={this.props.videos.video.url} frameBorder="0" allowFullScreen></iframe>
						</div>
						<div className="description">
							<h1>{this.props.videos.video.title}</h1>
						</div>
					</div>
					<div className="control"><div className="button next" onClick={() => {this.getRandomVideo()}}></div></div>
			</div>
		);
	}
}

export default connect(
	state => ({
		videos: state.videos
	}),
	dispatch => ({dispatch})
)(Player);
